<template>

	<view class="content">
		<view class="bgcf">
			<view class=" b-t-1">
						<view class="flex pl15 pt10 pr15 pb10 aic">
							<text class="fs16 fwb">姓名</text>
							<text class="fs16 fwb cf">姓名</text>
							<input type="text" class="fs14 pl35  ml20" v-model="xingm" placeholder="请输入姓名" placeholder-style="color: #ddd;" />
						</view>
						
					</view>
					<view class=" b-t-1">
						<view class="flex pl15 pt10 pr15 pb10 aic">
							<text class="fs16 fwb">银行卡号</text>
							<input type="text" class="fs14 pl35 ml20" v-model="yinh" placeholder="请输入银行卡号" placeholder-style="color: #ddd;"/>
						</view>
						
					</view>
					<view class=" b-t-1">
						<view class="flex pl15 pt10 pr15 pb10 aic">
							<text class="fs16 fwb">手机号</text>
							<input type="text" class="fs14 pl35 ml35" v-model="shouj" placeholder="请输入手机号" placeholder-style="color: #ddd;"/>
						</view>
						
					</view>
					<view class=" b-t-1">
						<view class="flex pl15 pt10 pr15 pb10 aic">
							<text style="min-width: 3rem;" class="fs16 fwb">验证码</text>
							<input type="text"  v-model="yanz" class="fs14 pl35 ml35 yanzhengma_input" placeholder="请输入验证码" placeholder-style="color: #ddd;"/>
							<button @click='codeClick' v-if='changess' class="fs12 bgcf"  style="color: #F43B31;min-width: 6rem;"> 发送验证码</button>
							<button @click='codeClick' :disabled='disabled' v-else class="fs12 " style="background-color: #ffffff;">{{second}}后重试</button>
						</view>
						
					</view>
		</view>
		<view class="fun flex aic ju_c cf fs15" @click="tijiao">
			提交
		</view>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				showText: true,
				second: 59,
				disabled: false,
				changess: true,
				value: '',
				yanz:'',
				xingm:'',
				shouj:'',
				yinh:'',
			}
		},
		onHide() {

		},
		onLoad() {
that=this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			tijiao() {
				if(that.xingm=='' ) {
					uni.showToast({
						title: '姓名不能为空',
						icon: 'none',
					});
					return
				}
				if(that.yinh=='' ) {
					uni.showToast({
						title: '银行卡号不能为空',
						icon: 'none',
					});
					return
				}
				if(that.shouj=='' ) {
					uni.showToast({
						title: '手机号不能为空',
						icon: 'none',
					});
					return
				}
				if(that.yanz=='' ) {
					uni.showToast({
						title: '验证码不能为空',
						icon: 'none',
					});
					return
				}
				uni.showToast({
					title: '提交成功',
					icon: 'none',
				});
				setTimeout(function() {
					wx.navigateBack({
						delta: 1
					})
				},500)
			},
			// 获取验证码
			codeClick() {
				
						that.changess = false;
						that.disabled = true
						// console.log("1")
						that.showText = false
						var interval = setInterval(() => {
							--that.second
						}, 1000)
			
						setTimeout(() => {
							clearInterval(interval)
							that.showText = true
							that.second = 60
							that.disabled = false
							that.changess = true;
						}, 60000)
						
			
			},
		}
	}
</script>

<style>
	page {
		background-color:  #F8F8F8;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.content {
		background-color:  #F8F8F8;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}
	.fun {
		width: 694rpx;
		height: 80rpx;
		background: #F43B31;
		border-radius: 10rpx;
		margin-left: 30rpx;
		margin-top: 80rpx;
	}
	

